.about-me{
    background: transparent;
    min-height: 100vh;
}

.section-about-me{
    display: flex;
    justify-content: space-around;
    margin-top: 10%;
    perspective: 5000px;
}



.letters-about-section{
    color: black;
    font-family: 'Courier New', Courier, monospace;
    font-size: 35px;
    font-weight:bold;
    margin-bottom: 30px;
    margin-left: 30px;
}

.container-card{
    background: gray;
    border: 3px solid black;
    border-radius: 5px;
    height: 408px;
    width: 297px;
}


.btn-about-me button{
    background: none;
    border-radius: 30px;
    border: 1px solid white ;
    color: white;
    height: 35px;
    margin-left: 100px;
    margin-top: 25px;
    outline: none;
    cursor: pointer;
    width: 120px;
}

.btn-about-me a {
    color: wheat;
    cursor: pointer;
    text-decoration: none;
}

.card-about-me h2{
    margin-top: 30px;
}

.letters-about-section-text{
    font-size: 28px;
    margin-bottom: -80px;
    margin-left: 15px;
    margin-top: 15px;
}

#btn-about-me button{
    margin-top: 100px;
}
@media(max-width:1024px){
    #btn-about-me button{
        margin-top: 30px;
    }
}
.list-container{
    display: flex;
    font-size: 15px;
    margin-left: 40px;
}

@media(max-width:1024px){
    .list-container{
        margin-left: 80px;
    }
}

.letters-about-section-name{
    color: rgb(16, 106, 89);
    margin-top: 25px;
    text-shadow: rgb(248, 250, 250) 0px 0px 10px;
    font-size: 18px;
    margin-bottom: 40px;
}

@media(max-width:1024px){
    .list-container p{
        font-size: 20px !important;
        margin-top: -20px !important;
    }
}

.list-container p{
    display: block;
    font-family: monospace;
    font-size: 20px;
    margin-left: -30px;
    white-space:nowrap;
    border-right: 4px solid;
    width: 24ch;

    animation:infinite typing 9s  steps(22), 
blink .5s infinite step-end alternate ;
     overflow:hidden;
}

@keyframes typing {
    from {width: 0;}
}

@keyframes blink {
    50% {border-color: transparent;}
}



#skill-dev{
    margin-bottom: 20px;
}

@media(max-width:1024px){
   .list-container p{
    margin-left: -70px;
   }  
}

.list-hobbies {
    color: white;
    font-size: 20px;
    letter-spacing: 0px;
    line-height: 50px;
    list-style: none;
    margin-left: -25px;
    margin-bottom: 15px;
    margin-top: 10px;
}  



@media(max-width:1024px){
    .list-hobbies {
        color: white;
        font-size: 15px !important;
        letter-spacing: 0px;
        line-height: 20px;
        list-style: none;
        margin-left: -25px;
        margin-bottom: 1px;
        margin-top: 10px;
    }  
}

.list-hobbies span {
       font-size: 40px;
} 

@media(max-width:1024px){
    .list-hobbies span {
        font-size: 20px;  
    }  
}

.list-skill li{
    color: white;
    font-size: 25px;
    margin-left: -25px;
    list-style: none;
}

.list-skill span{
    font-size: 30px;
}
 
.skill-sport span{
    font-size: 40px;
    margin-top: 10px;
}

@media(max-width:1024px){
    .list-skill span{
        font-size: 20px;
       }   
}

 @media(max-width:1024px){
    .skill-sport span{
        font-size: 35px;
        margin-top: 40px;
       }
}

@media(max-width:1024px){
    .list-skill li{
     color: white;
     font-size: 10px;
     margin-left: -25px;
     list-style: none;
    }
}

@media(max-width:1024px){
   .btn-about-me button{
    width: 10px;
    height: 10px;
   }
}


#card-about-me3{
    background: rgb(61, 199, 148);
    border-radius: 5px;
    box-shadow: 1px 1px 2px rgb(153, 246, 212);
    height: 400px;
    position: absolute;
    width: 290px;
    transition: all 1s;
    transform: rotate(0deg);

    transform-style:preserve-3d;
}

#card-about-me3:hover{
    color: black;
    box-shadow:  1px 1px 5px  rgb(3, 189, 154);
    transform: rotateY(360deg);
   
}


#card-about-me1{
    background: rgb(61, 199, 148);
    border-radius: 5px;
    box-shadow: 1px 1px 2px rgb(153, 246, 212);
    height: 400px;
    width: 290px;
    transition: all 1s;
    transform: rotateX(30deg);

    transform-style:preserve-3d;
}

#card-about-me1:hover{
    transform: rotateY(160deg);
}

#card-about-me2{
    background: rgb(61, 199, 148);
    border-radius: 5px;
    box-shadow: 1px 1px 2px rgb(153, 246, 212);
    height: 400px;
    margin-right: 80px;
    margin-top: 200px;
    width: 300px;
    transition: all 1s;
    transform: rotate(30deg);

    transform-style:preserve-3d;
}

#card-about-me2:hover{
    transform: rotateY(360deg);
}



@media(max-width:1024px){
    .letters-about-section{
        font-size: 15px;
    }
}

@media(max-width:1024px){
    .section-about-me{
        display: flex;
        flex-direction: column;
        margin-top: 1px;
    }
}

@media(max-width:1024px){
    #card-about-me1{
        text-align: center;
        height: 260px;
        margin-bottom: 30px;
        margin-left: 20px;
        width: 360px
    }
}

@media(max-width:1024px){
    #card-about-me1 h2{
        font-size: 40px;
    }
}

@media(max-width:1024px){
    #card-about-me2{
        height: 270px;
        margin-left: 80px;
        margin-top: 40px;
        width: 200px;      
    }
}
@media(max-width:1024px){
    #card-about-me3{
        height: 270px;
        margin-left: 150px;
        margin-top: 120px;
        width: 200px;
    }
}

@media(max-width:1024px){
    .btn-about-me button{
        height: 30px;
        margin-left: 40px;
        width: 80px;
    }
}

@media(max-width:1024px){
    .container-card{
        background: none;
        border:none;
        border-radius: none;
        height: none;
        width: none;
    }
}
